.Header {
  z-index: 1;
  padding-top: 12px;
  padding-bottom: 12px;
  color: rgba(255, 255, 255, 0.75);
  background-color: #24292e; }

.header-logo-invertocat {
  margin: -1px 15px -1px -2px;
  color: #fff;
  white-space: nowrap; }

.header-logo-invertocat .octicon-mark-github {
  float: left; }

.header-search {
  width: 300px;
  font-size: 13px; }

.header-search-wrapper {
  display: table;
  width: 100%;
  max-width: 100%;
  min-height: 0;
  padding: 0;
  font-weight: normal;
  vertical-align: middle; }

.Header .header-search-wrapper {
  min-height: 30px;
  font-size: inherit;
  color: #fff;
  background-color: rgba(255, 255, 255, 0.125);
  border: 0;
  -webkit-box-shadow: none;
          box-shadow: none; }

.header-search-input {
  display: table-cell;
  width: 100%;
  min-height: 26px;
  padding-top: 0;
  padding-bottom: 0;
  background: none;
  border: 0;
  -webkit-box-shadow: none;
          box-shadow: none; }

.Header .header-search-input {
  min-height: 30px;
  font-size: 16px;
  color: inherit; }

@media (min-width: 1012px) {
  .Header .header-search-input {
    font-size: inherit; } }

.HeaderNavlink {
  display: block;
  color: rgba(255, 255, 255, 0.75); }

.HeaderNavlink:hover,
.HeaderNavlink:focus {
  color: #fff;
  text-decoration: none; }

.user-nav .octicon {
  width: 16px; }

/* trick: enable "Show user agent shadow DOM" in chrome dev tools */
.Header .header-search-input::-webkit-input-placeholder {
  color: rgba(255, 255, 255, 0.75); }
.Header .header-search-input:-ms-input-placeholder {
  color: rgba(255, 255, 255, 0.75); }
.Header .header-search-input::-ms-input-placeholder {
  color: rgba(255, 255, 255, 0.75); }
.Header .header-search-input::placeholder {
  color: rgba(255, 255, 255, 0.75); }

.dropdown-menu {
  position: absolute;
  top: 100%;
  left: 0;
  z-index: 100;
  width: 160px;
  padding-top: 5px;
  padding-bottom: 5px;
  margin-top: 2px;
  list-style: none;
  background-color: #fff;
  background-clip: padding-box;
  border: 1px solid rgba(27, 31, 35, 0.15);
  border-radius: 4px;
  -webkit-box-shadow: 0 3px 12px rgba(27, 31, 35, 0.15);
          box-shadow: 0 3px 12px rgba(27, 31, 35, 0.15); }

.dropdown-menu-sw {
  right: 0;
  left: auto; }

.user-nav .dropdown-menu {
  width: 180px;
  margin-top: 8px; }

.dropdown-item {
  display: block;
  padding: 4px 10px 4px 15px;
  overflow: hidden;
  color: #24292e;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  white-space: nowrap; }

.dropdown-menu::before,
.dropdown-menu::after {
  position: absolute;
  display: inline-block;
  content: ""; }

.dropdown-menu::before {
  border: 8px solid transparent;
  border-bottom-color: rgba(27, 31, 35, 0.15); }

.dropdown-menu-sw::before {
  top: -16px;
  right: 9px;
  left: auto; }

.dropdown-menu::after {
  border: 7px solid transparent;
  border-bottom-color: #fff; }

.dropdown-menu-sw::after {
  top: -14px;
  right: 10px;
  left: auto; }

.dropdown-item:hover {
  color: #fff;
  text-decoration: none;
  background-color: #0366d6; }

.dropdown-header {
  padding: 4px 15px;
  font-size: 12px;
  color: #586069; }

.header-nav-current-user {
  padding-bottom: 0;
  font-size: inherit; }

.header-nav-current-user .css-truncate-target {
  max-width: 100%; }

.dropdown-divider {
  height: 1px;
  margin: 8px 1px;
  background-color: #e1e4e8; }

.dropdown-signout {
  width: 100%;
  text-align: left;
  background: none;
  border: 0; }

.header-search-input:focus ~ .header-search-key-slash {
  display: none; }

.jump-to-field-active ~ .header-search-key-slash {
  display: none; }

.jump-to-field-active {
  color: #24292e !important;
  background-color: #fafbfc; }

.jump-to-field-active::-webkit-input-placeholder {
  color: #586069 !important; }

.jump-to-field-active:-ms-input-placeholder {
  color: #586069 !important; }

.jump-to-field-active::-ms-input-placeholder {
  color: #586069 !important; }

.jump-to-field-active::placeholder {
  color: #586069 !important; }

.jump-to-field-active.jump-to-field-active-non-empty {
  border-bottom-right-radius: 0;
  border-bottom-left-radius: 0; }

.Box {
  background-color: #fff;
  border: 1px solid #d1d5da;
  border-radius: 3px; }

.jump-to-suggestions {
  top: 30px;
  left: 0;
  z-index: 35;
  width: 100%;
  border-top-left-radius: 0;
  border-top-right-radius: 0;
  border-bottom-right-radius: 3px;
  border-bottom-left-radius: 3px;
  -webkit-box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
          box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1); }

.jump-to-suggestions-results-container .navigation-item {
  border-bottom: 1px solid #e1e4e8; }

.jump-to-suggestions-path {
  width: 300px;
  min-height: 44px;
  color: #1b1f23; }

.jump-to-suggestions-path .jump-to-octicon {
  width: 28px;
  color: #6a737d; }

.jump-to-suggestions-path .jump-to-suggestion-name {
  max-width: 240px; }

.jump-to-suggestions-results-container .navigation-focus .jump-to-suggestions-path {
  color: #fff;
  background: #0366d6; }

.jump-to-suggestions-results-container .d-on-nav-focus {
  display: none; }

.jump-to-suggestions-results-container .navigation-focus .d-on-nav-focus {
  display: block; }

.jump-to-suggestions-results-container .navigation-focus .jump-to-octicon {
  color: #fff; }



