* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

body {
  margin: 0;
  font-family: -apple-system,BlinkMacSystemFont,"Segoe UI",Helvetica,Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol";
  font-size: 14px;
  line-height: 1.5;
  color: #24292e;
  background-color: #fff;
  min-width: 1020px;
  word-wrap: break-word; }

#root, .App {
  margin: 0;
  /* background-color: #fff; */
  min-width: 1020px; }

article, aside, details, figcaption, figure, footer, header, main, menu, nav, section {
  display: block; }

a {
  background-color: transparent;
  color: #0366d6;
  text-decoration: none;
  cursor: pointer; }

a:hover {
  text-decoration: underline; }

p {
  margin-top: 0;
  margin-bottom: 10px; }

svg:not(:root) {
  overflow: hidden; }

button, input, select, textarea {
  font: inherit;
  margin: 0; }

button, input {
  overflow: visible; }

input, select, textarea, button {
  font-family: inherit;
  font-size: inherit;
  line-height: inherit; }

button, select {
  text-transform: none; }

button {
  cursor: pointer;
  border-radius: 0; }

button:focus {
  outline: none; }

ul, ol {
  padding-left: 0;
  margin-top: 0;
  margin-bottom: 0; }

details summary {
  cursor: pointer; }

img {
  border-style: none; }

h1 {
  margin: 0.67em 0; }

h1, h2, h3, h4, h5, h6 {
  margin-top: 0;
  margin-bottom: 0; }

h1 {
  font-size: 32px;
  font-weight: 600; }

b, strong {
  font-weight: 600; }



